.App {

    h1,
    h2 {
        text-align: center;
        color: #8C61C0;
    }

    h1 {
        margin-top: 10px;
    }
}

.Display {

    margin-top: 20px;
    padding: 20px;
    display: flex;
    flex-direction: column;

    h3 {
        text-align: center;
        margin: 10px 0px;
    }

    .inputs {
        display: flex;
        flex-wrap: wrap;
        max-width: 900px;

        gap: 10px;
        margin: 10px auto 20px;

        input {
            width: 200px;
            outline: none;
            height: 25px;
            padding-left: 10px;
            border: 2px solid grey;
            border-radius: 10px;
        }
    }

    .button {
        padding: 6px 20px;
        margin: 0px auto;
        border-radius: 10px;
        border: none;
        cursor: pointer;
        background-color: gold;
    }


    .display {
        // margin-top: 20px;
        display: flex;
        gap: 10px;
        justify-content: center;
        flex-wrap: wrap;

        .noDataDiv {
            width: 100%;
            height: 10vw;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .show {
            width: 330px;
            background-color: #2C2C2C;
            border-radius: 10px;
            color: #DADADA;

            div {
                display: flex;

                padding: 15px;

                .sideHeading {
                    width: 110px;
                    display: flex;
                    justify-content: space-between;
                }

                span:not(.sideHeading) {
                    padding-right: 10px;
                }
            }
        }

        &::after {
            width: 330px;
            content: "";
            height: 1px;
        }

    }
}

.footer {

    color: gray;
    font-size: 13px;
    position: relative;
    bottom: 5px;

    p {
        text-align: center;
    }
}

.Ingestor {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h3 {
        margin: 30px 0px;
    }

    .button {
        padding: 6px 20px;
        margin: 0px auto;
        border-radius: 10px;
        border: none;
        cursor: pointer;
        margin-top: 20px;
        background-color: gold;
    }

    form {
        display: flex;
        flex-direction: column;
        gap: 10px;

        .inputs {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 10px;

            div {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;

                label {
                    width: 150px;
                }

                span {
                    width: 20px;
                }

                input {
                    width: 200px;
                    padding: 5px 10px;
                    border-radius: 10px;
                    border: none;
                    outline: none;
                }
            }
        }
    }
}